.lineup{
    background-color: $rosa;
    padding: 10rem 0;
    h3,p{
        text-align: center;
        color: $blanco;

    }
    .dia{
        background-color: $negro;
        margin: 0px auto;
        width: 50%;
        padding: 1rem;
        margin-top: -2rem;
        margin-bottom: 5rem;
        font-weight: 600;
        text-transform: uppercase;
        animation: pulse 1.5s infinite;
        
    }
}
.contenedor1{
    width: 70%;
    @include desktop{
        width: 90%;
    }
}

.escenarios-contenedor{
    @include desktop{
        @include grid(2, 10rem);
        
    }
    @include telefonomax{
        @include grid(1, 5rem);
        
    }
    p{
        font-size: 2.2rem;
        color: $negro;
    }
    .escenario{
        border-radius: 0.5rem;

        .nombre-escenario{
            font-weight: 300;
            padding: 2rem;
            -webkit-transition: font-size 0.5s ease;
            transition: font-size 0.5s ease;
            &::after{
                content: '';
                display: block;
                width: 20rem;
                height: 27rem;
                background-size: 15rem;
                background-repeat: no-repeat;
                background-position: center center;
                margin: 2rem auto 0 auto;
                
            }
            &:hover{
                font-size: 30px;
                text-shadow: 0px 0px 10px $negro;
            }
        }
        
    }
    
}
.escenario{
    margin-bottom: 3rem;
    
}
.rock,
.edm{
    transition: 0.5s ease-in-out;
    &:hover{
        -webkit-transform:scale(0.9);transform:scale(0.9);
    }

}
.edm .nombre-escenario:after{
    background-image: url(../../../build/img/user0.webp);

}
.rock .nombre-escenario:after{
    background-image: url(../../../build/img/user0.webp);

}
.pegamento .nombre-escenario::after{
    background-image: url(../../../build/img/user0.webp);
}


.bg-amarillo{
    background-color: $blanco;
}
.bg-verde{
    background-color: $blanco;
}