.datos-title{
    padding: 8rem 0;
    h3{
        background-color: $rosa;
        color: $blanco;
        margin-top: 3rem;
        padding: 2rem;
        padding-top: 2rem;
        text-align: center;
        animation: pulse 1.5s infinite;
    }

}
.datos{
    padding: 3rem 0;
    @include tablet{
        @include grid(2, 5rem);
    }
    .imagen-mv{
        transition: 0.5s ease-in-out;
        overflow:hidden;

    }
    .imagen-mv:hover{
        filter: grayscale(80%);
        -webkit-transform:scale(0.9);transform:scale(0.9);
        transition: 300ms all ;
    }
    
}
.contenido-mision{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 5rem;
    @include tablet {
        margin-top: 0;
    }
}