@mixin telefono {
    @media(min-width: $telefono){
        //* Con content podemos agregar codigo media sin tener errores
        @content;
    }
}
@mixin telefonomax {
    @media(max-width: $telefono){
        //* Con content podemos agregar codigo media sin tener errores
        @content;
    }
}
@mixin tablet{
    @media(min-width: $tablet){
        @content;
    }
}
@mixin tabletmax {
    @media(max-width: $tablet){
        //* Con content podemos agregar codigo media sin tener errores
        @content;
    }
}
@mixin desktop{
    @media(min-width: $desktop){
        @content;
    }
}
@mixin desktopmax {
    @media(max-width: $desktop){
        //* Con content podemos agregar codigo media sin tener errores
        @content;
    }
}
@mixin desktopXL{
    @media(min-width: $desktopXL){
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}
@mixin resetear-lista {
    margin: 0;
    padding: 0;
    list-style: none;
}