html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat";
  font-size: 1.6rem;
  background: linear-gradient(113deg, #fdfdfd 0%, rgba(241, 219, 255, 0.904) 100%);
}
body.fijar-body {
  overflow: hidden;
}
@media (min-width: 992px) {
  body .body-scroll {
    padding-top: 84px;
  }
}

p {
  font-size: 2rem;
  color: #000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0px auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  display: block;
  cursor: pointer;
}

.header {
  background-color: #f5f3f3;
  /* Menu Hamburgesa */
  /*Fin Del Menu Hamburgesa */
}
@media (min-width: 992px) {
  .header.fijo {
    z-index: 10;
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.5rem #333;
    transition: 1s ease-in-out;
    -webkit-transform: scale(0.9);
    transform: scale(0.9);
    transition: 900ms all;
    opacity: 10%;
  }
  .header.fijo.fijo:hover {
    transition: 0.5s ease-in-out;
    opacity: 100%;
  }
}
.header h1 {
  color: #F53756;
  text-align: center;
}
@media (min-width: 992px) {
  .header h1 {
    margin: 0;
  }
}
.header .contenido-header {
  padding: 1rem;
}
@media (min-width: 992px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header .contenido-header img {
  height: 60px;
}
@media (min-width: 992px) {
  .header .contenido-header img {
    margin: 0;
  }
}
.header .toggle-btn {
  width: 35px;
  position: absolute;
  right: 35px;
  top: 27px;
  display: none;
  transition: 0.5s ease-in-out;
}
.header .toggle-btn:hover {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}
.header .toggle-btn span {
  float: left;
  display: block;
  width: 100%;
  height: 3px;
  background-color: #F53756;
  margin-bottom: 8px;
  border-radius: 10rem;
}
@media (max-width: 991px) {
  .header .toggle-btn {
    display: block;
  }
  .header nav {
    display: none;
    width: 100%;
    margin-top: 1rem;
  }
  .header nav.show {
    display: block;
  }
}

@media (min-width: 992px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  color: #000;
  font-size: 1.9rem;
  margin-bottom: 1rem;
  padding: 1.8rem;
}
@media (min-width: 992px) {
  .navegacion-principal a {
    margin: 0px;
  }
}
.navegacion-principal a:hover {
  background-color: #686868;
  color: #fff;
  border-radius: 0.5rem;
  transition: 0.5s ease-in-out;
  overflow: hidden;
  animation: latir3 infinite 1.2s linear;
}
@keyframes latir3 {
  0% {
    /* transform nos permite manipular un elemento sin que afecten a otro elementos */
    transform: scale(1, 1);
  }
  50% {
    transform: scale(0.92, 0.92);
  }
  100% {
    transform: scale(1, 1);
  }
}

.video {
  position: relative;
  max-height: 45rem;
  overflow: hidden;
}
.video .overlay1 {
  position: absolute;
  height: 100%;
  width: 100%;
  display: grid;
  align-content: center;
  justify-content: start;
  margin: 0px auto;
  margin-left: 6rem;
}
@media (max-width: 480px) {
  .video .overlay1 {
    margin-left: 0rem;
    justify-content: center;
    justify-items: center;
  }
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 768px) {
  .video video {
    width: 100.1%;
    margin: 0px auto;
  }
}
.video img {
  margin: 0px auto;
}

.contenido-video {
  z-index: 30;
}
.contenido-video h2 {
  z-index: 30;
  display: block;
  color: #fff;
  font-size: 6rem;
  margin-bottom: 0.5rem;
  margin-left: 0.5rem;
  text-shadow: 0px 0px 30px black;
}
@media (max-width: 992px) {
  .contenido-video h2 {
    font-size: 5.5rem;
  }
}
@media (max-width: 768px) {
  .contenido-video h2 {
    font-size: 5rem;
  }
}
@media (max-width: 480px) {
  .contenido-video h2 {
    margin-left: 0rem;
    font-size: 3rem;
    margin-bottom: 0.5rem;
  }
}
.contenido-video p {
  z-index: 20;
  background-color: #F53756;
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease;
  display: inline-block;
  padding: 1rem 2rem;
  color: #fff;
  font-weight: 700;
  font-size: 1.7rem;
  margin: 0;
  animation: pulse 2s infinite;
}
.contenido-video p:hover {
  background-color: #444;
}
@media (max-width: 992px) {
  .contenido-video p {
    font-size: 1.3rem;
  }
}
@media (max-width: 768px) {
  .contenido-video p {
    font-size: 1.2rem;
  }
}
@media (max-width: 480px) {
  .contenido-video p {
    font-size: 1.2rem;
  }
}
@keyframes pulse {
  0% {
    transform: scale(0.99);
    box-shadow: 0 0 0 0 #e07aff;
  }
  70% {
    transform: scale(1);
    box-shadow: 0 0 0 20px rgba(0, 0, 0, 0);
  }
  100% {
    transform: scale(0.99);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
}

.sobre-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.sobre-festival .imagen-sn {
  transition: 0.5s ease-in-out;
  overflow: hidden;
}
.sobre-festival .imagen-sn:hover {
  filter: grayscale(80%);
  -webkit-transform: scale(0.9);
  transform: scale(0.9);
  transition: 300ms all;
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival h2 {
  margin-bottom: 2rem;
}
.contenido-festival .fecha {
  color: #4CB8B3;
  font-weight: 700;
}

.lineup {
  background-color: #F53756;
  padding: 10rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #fff;
}
.lineup .dia {
  background-color: #000;
  margin: 0px auto;
  width: 50%;
  padding: 1rem;
  margin-top: -2rem;
  margin-bottom: 5rem;
  font-weight: 600;
  text-transform: uppercase;
  animation: pulse 1.5s infinite;
}

.contenedor1 {
  width: 70%;
}
@media (min-width: 992px) {
  .contenedor1 {
    width: 90%;
  }
}

@media (min-width: 992px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10rem;
  }
}
@media (max-width: 480px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 5rem;
  }
}
.escenarios-contenedor p {
  font-size: 2.2rem;
  color: #000;
}
.escenarios-contenedor .escenario {
  border-radius: 0.5rem;
}
.escenarios-contenedor .escenario .nombre-escenario {
  font-weight: 300;
  padding: 2rem;
  -webkit-transition: font-size 0.5s ease;
  transition: font-size 0.5s ease;
}
.escenarios-contenedor .escenario .nombre-escenario::after {
  content: "";
  display: block;
  width: 20rem;
  height: 27rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}
.escenarios-contenedor .escenario .nombre-escenario:hover {
  font-size: 30px;
  text-shadow: 0px 0px 10px #000;
}

.escenario {
  margin-bottom: 3rem;
}

.rock,
.edm {
  transition: 0.5s ease-in-out;
}
.rock:hover,
.edm:hover {
  -webkit-transform: scale(0.9);
  transform: scale(0.9);
}

.edm .nombre-escenario:after {
  background-image: url(../../../build/img/user0.webp);
}

.rock .nombre-escenario:after {
  background-image: url(../../../build/img/user0.webp);
}

.pegamento .nombre-escenario::after {
  background-image: url(../../../build/img/user0.webp);
}

.bg-amarillo {
  background-color: #fff;
}

.bg-verde {
  background-color: #fff;
}

.datos-title {
  padding: 8rem 0;
}
.datos-title h3 {
  background-color: #F53756;
  color: #fff;
  margin-top: 3rem;
  padding: 2rem;
  padding-top: 2rem;
  text-align: center;
  animation: pulse 1.5s infinite;
}

.datos {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .datos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.datos .imagen-mv {
  transition: 0.5s ease-in-out;
  overflow: hidden;
}
.datos .imagen-mv:hover {
  filter: grayscale(80%);
  -webkit-transform: scale(0.9);
  transform: scale(0.9);
  transition: 300ms all;
}

.contenido-mision {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido-mision {
    margin-top: 0;
  }
}

.galeria {
  background-color: #F53756;
  padding: 15rem 0;
}
.galeria h3 {
  margin: 0px auto;
  width: 80%;
  margin-bottom: 3rem;
  padding: 1rem;
  background-color: #000;
  color: #fff;
  text-align: center;
  animation: pulse 1.5s infinite;
}
@media (max-width: 480px) {
  .galeria h3 {
    font-size: 2.5rem;
  }
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  margin: 0px auto;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}
.galeria-imagenes picture {
  transition: 0.3s ease-in-out;
  overflow: hidden;
}
.galeria-imagenes picture:hover {
  filter: grayscale(80%);
  -webkit-transform: scale(0.9);
  transform: scale(0.9);
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.315);
}
.overlay picture {
  margin-top: 9rem;
  display: block;
  max-width: 70rem;
}

.btn-cerrar {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  background-color: #fff;
  color: #000;
  display: grid;
  place-items: center;
  font-weight: 600;
  transition: 0.5s ease-in-out;
  overflow: hidden;
}
.btn-cerrar:hover {
  cursor: pointer;
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}

.contacto {
