.sobre-festival{
    padding: 5rem 0;
    
    @include tablet{
        @include grid(2, 5rem);
    }
    .imagen-sn{
        transition: 0.5s ease-in-out;
        overflow:hidden;
    }
    .imagen-sn:hover{
        filter: grayscale(80%);
        -webkit-transform:scale(0.9);transform:scale(0.9);
        transition: 300ms all ;
    }
}
.contenido-festival{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 5rem;
    @include tablet {
        margin-top: 0;
    }
    h2{
        margin-bottom: 2rem;
    }
    .fecha{
        color: $verde;
        font-weight: 700;
    }
}