.video{
    position: relative;
    max-height: 45rem;
    overflow: hidden;

    .overlay1{
        position: absolute;
        height: 100%;
        width: 100%;
        display: grid;
        align-content: center;
        justify-content: start;
        margin: 0px auto;
        margin-left: 6rem;
        @include telefonomax{
            margin-left: 0rem;
            justify-content: center;
            justify-items: center;
        }
    }
    video{
        width: auto;
        display: block;
        @include tablet{
            width: 100.1%;
            margin: 0px auto;
        }
    }
    img{
        margin: 0px auto;
    }
    
    
    

    
}

.contenido-video{
    z-index: 30;
    h2{
       
        z-index: 30;
        display: block;
        color: $blanco;
        font-size: 6rem;
        margin-bottom: 0.5rem;
        margin-left: 0.5rem;
        text-shadow: 0px 0px 30px black;
        @include desktopmax{
            font-size: 5.5rem;
        }
        @include tabletmax{
            font-size: 5rem;
        }

        @include telefonomax{
            margin-left: 0rem;
            font-size: 3rem;
            margin-bottom: 0.5rem;
            
        }
        
    }
    
    p{
        z-index: 20;
        background-color: $rosa;
        -webkit-transition:  0.5s ease;
        transition:  0.5s ease;
        &:hover{
            background-color: #444;
        }
        display: inline-block;
        padding: 1rem 2rem;
        color: $blanco;
        font-weight: 700;
        font-size: 1.7rem;
        margin: 0;
        @include desktopmax{
            font-size: 1.3rem;
        }
        @include tabletmax{
            font-size: 1.2rem;
        }
        
        @include telefonomax{
            font-size: 1.2rem;
            
        }
        
        
        
        animation: pulse 2s infinite;
        &{
            @keyframes pulse {
                0%{
                    transform: scale(.99);
                    box-shadow: 0 0 0 0 rgb(224, 122, 255);
                }
                70%{
                    transform: scale(1);
                    box-shadow: 0 0 0 20px rgba(0, 0, 0, 0);
                }
                100%{
                    transform: scale(.99);
                    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
                }
            }
        }

    }
}