.galeria{
    background-color: $rosa;
    padding: 15rem 0;
    h3{
        margin: 0px auto;
        width: 80%;
        margin-bottom: 3rem;
        padding: 1rem;
        background-color: $negro;
        color: $blanco;
        text-align: center;
        animation: pulse 1.5s infinite;
        &{
            @include telefonomax{
                font-size: 2.5rem
            }
        }
    }
}
.galeria-imagenes{
    @include resetear-lista;
    @include grid(2, 2rem);
    @include telefono{
        @include grid(3, 2rem);
    }
    @include tablet{
        @include grid(4, 2rem);
    }
    @include desktop{
        @include grid(6, 2rem);
    }
    margin: 0px auto;
    picture{
        transition: 0.3s ease-in-out;
        overflow:hidden;

    }
    picture:hover{
        filter: grayscale(80%);
        -webkit-transform:scale(0.9);transform:scale(0.9);
    }
    
}

.overlay{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;

    //Con Place-items lo posicionamos encima y lo centra
    display: grid;
    place-items: center;

    background-color: rgba(0, 0, 0, 0.315);

    picture{
        margin-top: 9rem;
        display: block;
        max-width: 70rem;
    }

}

.btn-cerrar{
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    background-color: $blanco;
    color: $negro;
    display: grid;
    place-items: center;
    font-weight: 600;
    transition: 0.5s ease-in-out;
    overflow:hidden;

    &:hover{
        cursor: pointer;
        -webkit-transform:scale(1.2);transform:scale(1.2);

    }
}