.contacto{
    background-color: $morado;
    padding: 10rem 0;

    h3{
        text-align: center;
    }
    h3{
        margin: 0px auto;
        width: 80%;
        background-color: $blanco;
        color: $negro;
        padding: 2rem;
        margin-bottom: 5rem;
        animation: pulse 1.5s infinite;

    }
}

.contenedor-contacto{
    margin: 0px auto;
    color: $blanco;
    @include tablet{
        @include grid(2, 5rem);
    }
    .im-contacto{
        transition: 0.5s ease-in-out;
        overflow:hidden;

        &:hover{
            filter: grayscale(80%);
            -webkit-transform:scale(0.9);transform:scale(0.9);
            transition: 300ms all ;
        }
    }
    
    p{
        color: $blanco;
        margin-top: -1rem
    }
}



.social{
    margin: 0px auto;
    @include telefono{
        @include grid(4, 1rem);
        gap: 0rem;
    }
    @include telefonomax{
        @include grid(4, 1rem);
        
    }
    .facebook{
        background-color: $blanco;
        border-radius: 100%;
        width: 40%;
        padding: 1rem;
        transition: 0.5s ease-in-out;
        overflow:hidden;

        &:hover{
            -webkit-transform:scale(1.3);transform:scale(1.3);
            transition: 300ms all ;
            box-shadow: 0px 0px 10px $blanco;
        }
    }
    .email{
        background-color: $blanco;
        border-radius: 100%;
        width: 40%;
        padding: 1rem;
        transition: 0.5s ease-in-out;
        overflow:hidden;
        &:hover{
            -webkit-transform:scale(1.3);transform:scale(1.3);
            transition: 300ms all ;
            box-shadow: 0px 0px 10px $blanco;
        }
    }
    .telefono{
        background-color: $blanco;
        border-radius: 100%;
        width: 40%;
        padding: 1rem;
        transition: 0.5s ease-in-out;
        overflow:hidden;
        &:hover{
            -webkit-transform:scale(1.3);transform:scale(1.3);
            transition: 300ms all ;
            box-shadow: 0px 0px 10px $blanco;
        }
    }
    .maps{
        background-color: $blanco;
        border-radius: 100%;
        width: 40%;
        padding: 1rem;
        transition: 0.5s ease-in-out;
        overflow:hidden;
        &:hover{
            -webkit-transform:scale(1.3);transform:scale(1.3);
            transition: 300ms all ;
            box-shadow: 0px 0px 10px $blanco;
        }
    }

}
