html{
    // 1rem = 10px
    font-size: 62.5%;
    box-sizing: border-box;
}

// Con esto nos evitamos problemas con el padding
*,
*::before,
*::after{
    box-sizing: inherit;
}
body{
    font-family: $fuente_principal;
    font-size: 1.6rem;
    background: linear-gradient(113deg, rgb(253, 253, 253) 0%, rgba(241, 219, 255, 0.904) 100%);

    &.fijar-body{
        overflow: hidden;
    }
    @include desktop{
        .body-scroll{
            padding-top: 84px;
        }
    }
    
    }
    
p{
    font-size: 2rem;
    color: $negro;
    line-height: 1.5;
}
.contenedor{
    width: 95%;
    max-width: 120rem;
    margin: 0px auto;
}
a{
    text-decoration: none;
}
h1, h2 ,h3{
    margin: 0 0 5rem 0;
}
h1{
    font-size: 4rem;
}
h2{
    font-size: 3.6rem;
}
h3{
    font-size: 3rem;
}

img{
    max-width: 100%;
    display: block;
    cursor: pointer;
}

//* End Globales