.header{
    background-color: #f5f3f3;
    @include desktop {
        &.fijo{
            z-index: 10;
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .5rem #333;
            transition: 1s ease-in-out;
            -webkit-transform:scale(0.9);transform:scale(0.9);
            transition: 900ms all ;
            opacity: 10%;
            &.fijo:hover{
                transition: 0.5s ease-in-out;
                opacity: 100%;
            }
        }
    }
    
    h1{
        color: $rosa;
        text-align: center;
        @include desktop{
            margin: 0;
        }
    }
    .contenido-header{
        padding: 1rem;
        @include desktop{
            display:flex;
            justify-content:space-between;
            align-items: center;
        }
        img{
            height: 60px;
            @include desktop{
                margin: 0;
            }
            
        }
    }
    /* Menu Hamburgesa */
    .toggle-btn{
        width: 35px;
        position: absolute;
        right: 35px;
        top: 27px;
        display: none;
        transition: 0.5s ease-in-out;
        &:hover{
            -webkit-transform:scale(1.2);transform:scale(1.2);
        }

    }
    .toggle-btn span{
        float: left;
        display: block;
        width: 100%;
        height: 3px;
        background-color: $rosa;
        margin-bottom: 8px;
        border-radius: 10rem;
        

    }
    @media (max-width: 991px){
        .toggle-btn{
            display: block;
        }
        nav{
            display: none;
            width: 100%;
            margin-top: 1rem;
        }
        nav.show{
            display: block;
        }
    }
    /*Fin Del Menu Hamburgesa */
}
.navegacion-principal{
    @include desktop{
        display: flex;
        gap: 2rem;
        
    }
    a{
        display: block;
        text-align: center;
        color: $negro;
        font-size: 1.9rem;
        margin-bottom: 1rem;
        padding: 1.8rem;
        @include desktop{
            margin:0px
        }

        // Con el & hacemos que sass no nos cree un espacio en blanco ya que :hover debe estar unido a su variable
        &:hover{
            background-color: rgb(104, 104, 104);
            color: $blanco;
            border-radius: 0.5rem;
            transition: 0.5s ease-in-out;
            overflow:hidden;
            animation: latir3 infinite 1.2s linear;
            
        }
        @keyframes latir3{
            0%{
                /* transform nos permite manipular un elemento sin que afecten a otro elementos */
                transform: scale(1, 1);
            }
            50%{
                transform: scale(0.92, 0.92);
            }
            100%{
                transform: scale(1, 1);
            }
        
        }
    }
}
